import * as React from 'react';
import { NextFunctionComponent, NextContext } from 'next';
import fetch from 'isomorphic-unfetch';

import Layout from '../components/Layout';
import Precio from '../components/Precio';

import { IPrecioBitcoin } from '../interfaces';

export interface IIndexPageProps {
  precioBitcoin: IPrecioBitcoin;
}
const IndexPage: NextFunctionComponent<IIndexPageProps> = ({
  precioBitcoin
}) => {
  return (
    <Layout>
      <div className="row">
        <div className="col-12">
          <h2>Precio del Bitcoin</h2>
          <Precio precioBitcoin={precioBitcoin} />
        </div>
        <div className="col-md-8">
          <h2>Noticas sobre Bitcoin</h2>
        </div>
        <div className="col-md-4">
          <h2>Pr√≥ximos Eventos Bitcoin</h2>
        </div>
      </div>
    </Layout>
  );
};

IndexPage.getInitialProps = async () => {
  const response = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
  const precio = await response.json();
  return {
    precioBitcoin: precio.data.quotes.USD
  };
};
export default IndexPage;
